import { useNavigate } from "react-router-dom";
import './CameraPerNotAllowed.css';

function CameraPerNotAllowed() {

    const navigate = useNavigate();

    const onAllowCamera = () => {
        // Prompt the user for permission to use the camera
        navigator.mediaDevices.getUserMedia({ video: true })
            .then(function (stream) {
                // Get the video element on the page
                // var videoElement = document.getElementById('video-element');

                // Set the source of the video element to the stream from the camera
                // videoElement.srcObject = stream;

                // Play the video
                // videoElement.play();
                console.log(stream);
                navigate("/arviewer");
            })
            .catch(function (error) {
                // Handle errors
                console.error('Error accessing camera:', error);
            });
    }

    const onNotAllowingCamera = () => {
        navigate("/error");
    }

    return <div className='base-panel'><div className="permissions-intro vertical-layout"><div className="inner-container"><div className="message-container vertical-layout"><img alt='' src="/assets/logo_nasa-ki3cs502c5.svg" rel="preload" className="logo" /> <img alt='' src="/assets/ico_camera-k4bcs148k5.png" className="anim" style={{ width: '50px', margin: 'auto 0px 0px' }} /> <div className="title anim"><h1>Permissions</h1></div> <div className="body anim"><p>AR requires access to your camera. Press continue to allow.</p></div> <div className="buttons"><div className="anim"><svg width="150" height="50" onClick={onNotAllowingCamera}><polygon points="3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text fontFamily="SFUIText" x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">CANCEL</text></svg> </div> <div className="anim"><svg width="150" height="50" onClick={onAllowCamera}><polygon points="3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text fontFamily="SFUIText" x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">CONTINUE</text></svg> </div></div></div>

        {/* <div className="message-container vertical-layout" style={{ display: 'none' }}><img alt='' src="/assets/logo_nasa-ki3cs502c5.svg" rel="preload" className="logo" /> <img alt='' src="/assets/T_ui_ico_tablet-gqfa878bo4.png" className="icon anim" style={{ height: '17.6vh' }} /> <div className="title anim"><h1>Permission Needed</h1></div> <div className="body anim"><p>AR requires access to your sensors. Press continue to allow.</p></div> <div className="buttons"><div className="anim"><svg width="150" height="50"><polygon points="3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text fontFamily="SFUIText" x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">CANCEL</text></svg> </div> <div className="anim"><svg width="150" height="50"><polygon points=" 3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text fontFamily="SFUIText" x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">CONTINUE</text></svg> </div></div></div> <div className="denied vertical-layout" style={{ display: 'none' }}><img alt='' src="/assets/logo_nasa-ki3cs502c5.svg" rel="preload" className="logo" /> <img alt='' src="/assets/T_ui_ico_refresh-fxw9l96ow4.png" className="icon anim" /> <div className="title anim"><h1>Refresh the Page</h1></div> <div className="body anim"><p>Refresh the page to enable your camera for AR</p></div> <div className="buttons"><div className="anim"><svg width="150" height="50"><polygon points=" 3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text fontFamily="SFUIText" x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">RELOAD</text></svg></div></div></div>*/}</div></div></div>;
}

export default CameraPerNotAllowed;