import React, { useEffect } from 'react';
import 'mind-ar/dist/mindar-image-three.prod';
import { CSS3DObject } from 'three/examples/jsm/renderers/CSS3DRenderer.js';
import './OurARViewer.css';
// const THREE = window.MINDAR.IMAGE.THREE;

const OurARViewer = () => {
  useEffect(() => {
    // console.log(window);
    start();
  }, []);

  const start = async () => {
    const mindarThree = new window.MINDAR.IMAGE.MindARThree({
      container: document.body,
      imageTargetSrc: './assets/apples_rack.mind',
      uiScanning: "no",
    });    
    // uiLoading: "no",
    const { renderer, cssRenderer, cssScene, camera } = mindarThree;

    const obj = new CSS3DObject(document.querySelector("#ar-div"));
    const cssAnchor = mindarThree.addCSSAnchor(0);
    cssAnchor.group.add(obj);

    await mindarThree.start();
    renderer.setAnimationLoop(() => {
      cssRenderer.render(cssScene, camera);
    });
  }

  return (
    <div id="ar-div">
      <iframe id='course-cont' title="course" crossOrigin='true' src="https://samples-9.infoprolearning.com/courses/ar-poc/category-apples/story.html" width={1280} height={720} frameBorder={0}></iframe> 
    </div>
  )
}
export default OurARViewer;