import React from 'react';
import ReactDOM from 'react-dom';
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import OurARViewer from './components/OurARViewer/OurARViewer';
import ErrorPage from "./components/error-page";
import PermissionsIntro from './components/PermiIntro/PermissionsIntro';
import ExperIntro from './components/ExperIntro/ExperIntro';
import CameraPerNotAllowed from './components/PermiIntro/CameraPerNotAllowed/CameraPerNotAllowed';

const router = createBrowserRouter([
  {
    path: "/",
    element: <App />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "/experintro",
        element: <ExperIntro />,
      },
      {
        path: "/permintro",
        element: <PermissionsIntro />,
      },
      {
        path: "/permerror",
        element: <CameraPerNotAllowed />,
      },
      {
        path: "/arviewer",
        element: <OurARViewer />,
      },
      {
        path: "/error",
        element: <ErrorPage />,
      }
    ],
  },
]);

ReactDOM.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
