import { useNavigate } from "react-router-dom";
import './PermissionsIntro.css';

function PermissionsIntro() {

  const navigate = useNavigate();

  // const onClickHandler = () => {
  //   setStateOfFirstInput(true);
  // }

  const onAllowCamera = () => {
    // Prompt the user for permission to use the camera
    navigator.mediaDevices.getUserMedia({ video: true })
      .then(function (stream) {
        // Get the video element on the page
        // var videoElement = document.getElementById('video-element');

        // Set the source of the video element to the stream from the camera
        // videoElement.srcObject = stream;

        // Play the video
        // videoElement.play();
        // console.log(stream);
        navigate("/arviewer");
      })
      .catch(function (error) {
        // Handle errors
        console.error('Error accessing camera:', error);
      });
  }

  const onNotAllowingCamera = () => {
    navigate("/permerror");
  }

  return <div className='base-panel'><div className="permissions-intro vertical-layout"><div className="inner-container"><div className="message-container vertical-layout"><img alt='' src="/assets/logo_nasa-ki3cs502c5.svg" rel="preload" className="logo" /> <div><img alt='' src="/assets/ico_camera-k4bcs148k5.png" className="anim" style={{ width: '50px', margin: 'auto 0px 0px', opacity: '1', top: '0px' }} /> <div className="title anim" style={{ opacity: '1', top: '0px' }}><h1>Permissions</h1></div> <div className="body anim" style={{ opacity: '1', top: '0px' }}><p style={{ opacity: '1', top: '0px' }}>To access these digital experiences in WebAR, please enable camera access.</p></div></div> <div className="buttons" style={{ opacity: '1', top: '0px' }}><div className="anim acceptButton" style={{ opacity: '1', top: '0px' }}><svg width="150" height="50" onClick={onAllowCamera}><polygon points="3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">ALLOW</text></svg></div> <div className="anim declineButton" style={{ opacity: '1', top: '0px' }}><svg onClick={onNotAllowingCamera} width="150" height="50"><polygon points="3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">DON'T ALLOW</text></svg> </div></div></div></div></div></div>;
}

export default PermissionsIntro;