import React, { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import './App.css';
import { Outlet } from "react-router-dom";

function App() {
  const navigate = useNavigate();

  useEffect(() => {
    navigate("/permintro");
  }, []);

  return (
    <div className="App">
      <Outlet />
    </div>
  );
}

export default App;
