import {useNavigate} from "react-router-dom"
import './ExperIntro.css';

function ExperIntro({ setStateOfFirstInput }) {
  const navigate = useNavigate();
  
  const onClickHandler = () => {
    navigate("/permintro");
  }

  return <div className="base-panel vertical-layout gdpr-intro"><div className="inner-container"><img alt='' src="assets/first_woman_text-nn9ep6e706.png" rel="preload" className="textImgs anim" style={{ marginTop: '8vh' }} /> <img alt='' src="assets/first_woman_red_line-ribh9508k7.png" rel="preload" className="textImgs anim" /> <img alt='' src="assets/first_woman_digital_exp-iribi7ie05.png" rel="preload" className="textImgs anim" /> <h1 className="anim">Please ensure low power mode is not enabled.</h1> <p className="anim">This experience uses the latest AR tech and works best on newer devices. We're sorry if yours isn't supported.</p> <div className="anim" style={{ marginTop: '20px auto auto', position: 'relative' }}><svg width="150" height="50" onClick={onClickHandler}><polygon points="3 50 3 14 14 3 150 3 150 36 136 50 " strokeWidth="2" className="cls-1"></polygon> <text fontFamily="SFUIText" x="75" y="19" textAnchor="middle" dominantBaseline="hanging" className="cls-2">I UNDERSTAND</text></svg></div></div></div>;
}
export default ExperIntro;